<template>
    <div>
      <p>
        vue-draggable、awe-dnd 都是现成的拖拽排序组件,但在使用的过程中总是感觉差一点意思,存在没有过度,直接跳的问题,
        正好vue中封装了一个我个人非常喜欢的组件 tranisiton-group,所以利用现成的组件自己封装了一个
      </p>
    </div>
</template>

<script>
export default {
  name: 'draggingCompWindow'
}
</script>

